import React, { useState, useEffect } from 'react';
import { Input } from '../../components/Interface/Input';
import { Label } from '../../components/Interface/Label';
import { Button } from '../../components/Interface/Button';
import { Switch } from '../../components/Interface/Switch';
import { wordPressService } from '../../services/wordpress';
import styles from './Settings.module.css';

const { ipcRenderer } = window.require('electron');
const Store = window.require('electron-store');
const store = new Store();

export default function Settings() {
  const [isTestingConnection, setIsTestingConnection] = useState(false);
  const [formData, setFormData] = useState({
    blogUrl: '',
    username: '',
    password: '',
    enableReminders: false,
    reminderTime: '09:00',
    keepOnTop: false,
    showTitleField: true
  });

  useEffect(() => {
    loadSettings();
  }, []);

  const loadSettings = () => {
    setFormData({
      blogUrl: store.get('blogUrl', ''),
      username: store.get('username', ''),
      password: '',
      enableReminders: store.get('enableReminders', false),
      reminderTime: store.get('reminderTime', '09:00'),
      keepOnTop: store.get('keepOnTop', false),
      showTitleField: store.get('showTitleField', true)
    });
  };

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const testConnection = async () => {
    try {
      setIsTestingConnection(true);
      await wordPressService.testConnection();
      console.log("Success: Successfully connected to WordPress!");
    } catch (error) {
      console.error("Error:", error.message);
    } finally {
      setIsTestingConnection(false);
    }
  };

  const handleResetCredentials = async () => {
    try {
      // Clear electron-store values
      store.delete('blogUrl');
      store.delete('username');
      
      // Clear keychain password
      const username = store.get('username');
      if (username) {
        await ipcRenderer.invoke('delete-credentials', username);
      }
      
      // Reset form data
      setFormData(prev => ({
        ...prev,
        blogUrl: '',
        username: '',
        password: ''
      }));

      console.log("Success: Credentials reset successfully!");
    } catch (error) {
      console.error('Error resetting credentials:', error);
    }
  };

  const handleSave = async () => {
    try {
      store.set('blogUrl', formData.blogUrl);
      store.set('username', formData.username);
      store.set('enableReminders', formData.enableReminders);
      store.set('reminderTime', formData.reminderTime);
      store.set('keepOnTop', formData.keepOnTop);
      store.set('showTitleField', formData.showTitleField);

      // Update window behavior immediately
      await ipcRenderer.invoke('set-always-on-top', formData.keepOnTop);

      if (formData.password) {
        await ipcRenderer.invoke('save-credentials', {
          username: formData.username,
          password: formData.password
        });
        setFormData(prev => ({ ...prev, password: '' }));
      }

      console.log("Success: Settings saved successfully!");
      
      // Close the settings window
      await ipcRenderer.invoke('close-settings');
    } catch (error) {
      console.error("Error saving settings:", error.message);
    }
  };

  return (
    <div className={styles.container}>
      <div className={styles.options}>
        <div className={styles.site}>
          <h3>WordPress Connection</h3>
          <div className={styles.fieldset}>
            <Label htmlFor="blogUrl" className={styles.label}>URL</Label>
            <Input
              type="url"
              id="blogUrl"
              name="blogUrl"
              value={formData.blogUrl}
              onChange={handleChange}
              placeholder="https://yourblog.com"
            />
          </div>

          <div className={styles.fieldset}>
            <Label htmlFor="username" className={styles.label}>Username</Label>
            <Input
              type="text"
              id="username"
              name="username"
              value={formData.username}
              onChange={handleChange}
            />
          </div>

          <div className={styles.fieldset}>
            <Label htmlFor="password" className={styles.label}>Application Password</Label>
            <Input
              type="password"
              id="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
            />
            <div className={styles.help}>Create an application password in your Wordpress &rarr; Users &rarr; Profile.</div>
          </div>
    
            <div className={styles.siteActions}>
              <Button
                onClick={testConnection}
                disabled={isTestingConnection}
              >
                {isTestingConnection ? 'Testing...' : 'Test'}
              </Button>

              <Button
                variant="default"
                onClick={handleResetCredentials}
                className={styles.resetButton}
              >
                Reset
              </Button>

              <Button variant="primary" onClick={handleSave}>
                Save
              </Button>
            </div>
        </div>

        <div className={styles.preferences}>
          <h3>Preferences</h3>
          <div className={styles.fieldsetSwitch}>
            <div className={styles.fieldsetSwitchText}></div>
            <Label htmlFor="keepOnTop" className={styles.switchLabel}>Keep window on top</Label>
            <div className={styles.help}>The window will stay visible if you switch to another app.</div>
            <Switch
              id="keepOnTop"
              name="keepOnTop"
              checked={formData.keepOnTop}
              onCheckedChange={(checked) => {
                setFormData(prev => ({ ...prev, keepOnTop: checked }));
                store.set('keepOnTop', checked);
                ipcRenderer.invoke('set-always-on-top', checked);
              }}
            />
          </div>

          <div className={styles.switchField}>
            <Label htmlFor="showTitleField" className={styles.switchLabel}>Show title field in editor</Label>
            <div className={styles.help}>All WordPress posts require a title. With this off, WP will generate a title for you.</div>
            <Switch
              id="showTitleField"
              name="showTitleField"
              checked={formData.showTitleField}
              onCheckedChange={(checked) => {
                setFormData(prev => ({ ...prev, showTitleField: checked }));
                store.set('showTitleField', checked);
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
} 